.tareaTexto{
    width: 100%;
    height: 100%;
    font-size: 20px;
    display: flex;
    align-items: center;
    overflow-wrap: anywhere;
    color: white;
}

.tareaIcono {
    width: 25px;
    height: 25px;
    margin: 5px;
    color: white;    
}

.tareaContenedor.completada {
    background-color: #5a01a7;
    text-decoration: line-through;
}